<script>
  import { fly, fade } from 'svelte/transition';
  import { t } from 'precompile-intl-runtime';
  import Subcomponents from './Subcomponents';
  import HelpIcon from 'icons/help.svg';

  import ui from '../../../stores/UI';
  import draft from '../../../stores/Draft';

  $: selectedMenu = $ui.selectedMenu;
  let sidebar;
  export let open = false;

  function selectMenu(index) {
    open = false;
    ui.update((draft) => {
      draft.selectedMenu = draft.selectedMenu === index ? -1 : index;
    });
  }

  function exportToFile() {
    const blob = new Blob([JSON.stringify($draft)], {
      type: 'text/json',
    });
    const link = document.createElement('a');

    link.download = 'weave.json';
    link.href = window.URL.createObjectURL(blob);
    link.dataset.downloadurl = ['text/json', link.download, link.href].join(
      ':'
    );

    const evt = new MouseEvent('click', {
      view: window,
      bubbles: true,
      cancelable: true,
    });

    link.dispatchEvent(evt);
  }

  function importFromFile(e) {
    let files = e.target.files;
    if (files.length > 0) {
      let file = files[0];
      let reader = new FileReader();
      reader.onload = (readFile) => {
        try {
          let newDraft = JSON.parse(readFile.target.result);
          draft.set(newDraft);
        } catch (e) {
          console.error(e);
        }
      };
      reader.readAsText(file);
    }
  }
</script>

<div class="sidebar-container">
  {#if open}
    <div
      class="sidebar"
      bind:this={sidebar}
      transition:fade={{
        x: -150,
        duration: 20,
        opacity: 1,
      }}
    >
      {#each Subcomponents as item, i}
        <button class="icon-button" on:click={() => selectMenu(i)}>
          <div class={'icon'}>
            {@html item.icon}
          </div>
          <span>{$t(`page.${item.name}.title`)}</span>
        </button>
      {/each}
      <button class="icon-button save-load" on:click={exportToFile}>
        <span>{$t('terms.save')}</span>
      </button>

      <div class="save-load">
        <input
          type="file"
          name="upload"
          id="upload"
          on:input={importFromFile}
        />
        <label class="icon-button button" for="upload"
          >{$t('terms.upload')}</label
        >
      </div>
      <div class="help">
        <a href={`/weaver/docs_${$ui.locale}`}>
          <div class="icon">
            {@html HelpIcon}
          </div>
        </a>
      </div>
    </div>
  {/if}
</div>

<style>
  input[type='file'] {
    display: none;
  }

  .icon {
    fill: var(--color-1);
    stroke: var(--color-1);
    width: 50px;
